  // METHOD TO GET ALL THE BLOGS INFORMATION
  public getAllBlogs(): Observable<any> {
    //return this.allBlog;
    let myResponse = this._http.get(this.baseUrl + '/all?authToken=' + this.myApi);
    console.log('the all bogs' + '' + myResponse);
    return (myResponse);
  }


  // METHOD TO GET A PARTICULAR BLOG 
  public getSingleBlogInformation(currentBlog): Observable<any> {
    let myResponse = this._http.get(this.baseUrl + '/view/' + currentBlog + '?authToken=' + this.myApi);
    console.log('the all bogs Id' + '' + myResponse);
    return (myResponse);

  }// end get blog information function

  // =======================HERE WE ARE USING CURD CREATE, EDIT, DELETE, UPDATE===========================

  // CREATE CODE IS HERE

  public createBlog(blogData): any {
    let myResponse = this._http.post(this.baseUrl + '/create/' + '?authToken=' + this.myApi, blogData);
    console.log('the all bogs Id' + '' + myResponse);
    return (myResponse);

  }// CREATE FUNCTION

  // DELETE BLOG CODE IS HERE
  public deleteBlog(currentBlog): any {
    let myResponse = this._http.post(this.baseUrl +'/' + currentBlog +'/delete/' + '?authToken=' + this.myApi, currentBlog);
    console.log('the all bogs Id' + '' + myResponse);
    return (myResponse);

  }// delete function 

  // EDIT BLOG CODE IS HERE
  public editBlog(currentBlog, blogData): any {
    let myResponse = this._http.put(this.baseUrl +'/' + currentBlog +'/edit/' + '?authToken=' + this.myApi, blogData);
    console.log('the all bogs Id' + '' + myResponse);
    return (myResponse);

  }// edit function 


