import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

// FORMS MODULE IMPORTING FOR FORM 
import { FormsModule } from '@angular/forms';

// setting the router module for the router link
import { RouterModule, Routes } from '@angular/router';

// EXTRA IMPORTING 
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';

//importing alert ngx- toastr
import { ToastrModule } from 'ngx-toastr';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
//all component will there which i created
import { AppComponent } from './app.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { HomeComponent } from './home/home.component';
import { BlogEditComponent } from './blog-edit/blog-edit.component';
import { BlogViewComponent } from './blog-view/blog-view.component';
import { AboutComponent } from './about/about.component';
import { BlogCreateComponent } from './blog-create/blog-create.component';
import {NgModel} from '@angular/forms';



//importning the service
import { BlogService } from './blog.service';
import { BlogHttpService } from './blog-http.service';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
//decorators
@NgModule({
  declarations: [
    AppComponent,
    NotFoundComponent,
    HomeComponent,
    BlogEditComponent,
    BlogViewComponent,
    AboutComponent,
    BlogCreateComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    HttpClientModule,
    FormsModule,
    BrowserAnimationsModule,
    ToastrModule,
    ToastrModule.forRoot(),
    
    // RouterModule forRoot method to declare the possible routes in application
    RouterModule.forRoot([
      { path: '', redirectTo: 'home', pathMatch: 'full' },
      { path: 'home', component: HomeComponent },
      { path: 'about', component: AboutComponent },
      { path: 'create', component: BlogCreateComponent },
      { path: 'errorPage', component: NotFoundComponent },
      { path: 'blog/:blogId', component: BlogViewComponent },
      { path: 'edit/:blogId', component: BlogEditComponent },
      { path: '**', component: NotFoundComponent }
    ])


  ],
  providers: [BlogService, BlogHttpService],
  bootstrap: [AppComponent]
})
export class AppModule { }
