import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { BlogHttpService } from '../blog-http.service';

import {ActivatedRoute, Router} from '@angular/router';

//importing alert ngx- toastr
import { ToastrService } from 'ngx-toastr';

import {FormsModule} from '@angular/forms';
@Component({
  selector: 'app-blog-create',
  templateUrl: './blog-create.component.html',
  styleUrls: ['./blog-create.component.css']
})
export class BlogCreateComponent implements OnInit {
  public blogTitle:string;
  public blogBodyHtml:string;
  public blogDescription:string;
  public blogCategory:string;
  public bp=["Comedy", "Drama" , "Action", "Technologies"]

  constructor(public blogHttpService:BlogHttpService, private _router:ActivatedRoute, private router:Router,private toastr: ToastrService) { 
    console.log("Blog create component called ");
    
  }

  ngOnInit() {

    console.log("Blog create ngOnInit called ");
  }

  public createBlog():any{
    let blogData={
      title: this.blogTitle,
      description: this.blogDescription,
      blogBody: this.blogBodyHtml,
      category: this.blogCategory
    }
    console.log(blogData);
    this.blogHttpService.createBlog(blogData).subscribe(
      data=>{
          //console.log("Blog Created");
          console.log(data);
          this.toastr.success('Blog posted successfully Success');

          setTimeout(()=>{
            this.router.navigate(['/blog',data.data.blogId]);
          },1000)
      },

      error=>{
        console.log("Some error Occured");
          console.log(error.errorMessage);
          this.toastr.error('Some error Occured Error');
      }
    )

  }// END OF BLOG CREATEBLOG METHOD

   

}
