  
import { Component, OnInit } from '@angular/core';
import { BlogHttpService } from '../blog-http.service';
import {Location} from '@angular/common';
import {ActivatedRoute, Router} from '@angular/router';
import {FormsModule} from '@angular/forms';
//importing alert ngx- toastr
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-blog-edit',
  templateUrl: './blog-edit.component.html',
  styleUrls: ['./blog-edit.component.css']
})
export class BlogEditComponent implements OnInit {
 public currentBlog;
//  public blogTitle:string;
//   public blogBodyHtml:string;
//   public blogDescription:string;
//   public blogCategory:string;
  public bp=["Comedy", "Drama" , "Action", "Technologies"]

  constructor(public blogHttpService:BlogHttpService, private _router:ActivatedRoute, private router:Router,private toastr: ToastrService) { 
    console.log("Blog Edit component called ");
    
  }

  ngOnInit() {
    //getting the blog id from the route
    let getBlogId = this._router.snapshot.paramMap.get('blogId');
    console.log(getBlogId);
    // calling the function to get the blog with this blogId out of the overall array
    //this.currentBlog=this.blogService.getSingleBlogInformation(getBlogId) ;
    
    
    this.blogHttpService.getSingleBlogInformation(getBlogId).subscribe(
      data => {
        console.log(data);
        this.currentBlog = data["data"];
        this.toastr.success('Blog opened successfully');
        console.log("this is edit blog"+this.currentBlog);
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage);
      }
    )
    
    console.log("blog OnInit  function called is here");

  } //ngOnInit function end here

  // Edit this blog start from here

  public editThisBlog(): any{
    this.blogHttpService.editBlog(this.currentBlog.blogId, this.currentBlog).subscribe(
      data => {
        console.log(data);
        this.toastr.success("Blog Edited succesfully Thank you");

        setTimeout(()=>{
          this.router.navigate(['/blog', this.currentBlog.blogId]);
        }, 1000)

        this.currentBlog = data["data"];
        this.toastr.success('Blog opened successfully');
        console.log(this.currentBlog);
        this.router.navigate(['/home'])
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage);
      }
    )//editThisBlog subscribe end here
  }// editThisBlog methid end here


  }// END OF BLOG edit class

   



