import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
//importing service here
import { BlogService } from '../blog.service';
import { BlogHttpService } from '../blog-http.service';

//importing location to locate the location of back
import {Location} from '@angular/common';

//importing alert ngx- toastr
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-blog-view',
  templateUrl: './blog-view.component.html',
  styleUrls: ['./blog-view.component.css'],
   providers : [Location]
})
export class BlogViewComponent implements OnInit, OnDestroy {
  public currentBlog;


  constructor(private _router: ActivatedRoute, private router: Router, public blogService: BlogService, public blogHttpService: BlogHttpService,private toastr: ToastrService, private location:Location) {
    console.log("blog constructor function called is here");

  }

  ngOnInit() {
    //getting the blog id from the route
    let getBlogId = this._router.snapshot.paramMap.get('blogId');
    console.log(getBlogId);
    // calling the function to get the blog with this blogId out of the overall array
    //this.currentBlog=this.blogService.getSingleBlogInformation(getBlogId) ;
    
    
    this.blogHttpService.getSingleBlogInformation(getBlogId).subscribe(
      data => {
        console.log(data);
        this.toastr.success('Blog opened successfully');
        this.currentBlog = data["data"];
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    )
    console.log(this.currentBlog);
    console.log("blog OnInit  function called is here");

  } //ngOnInit function end here

// saurabh delete code is here
  public deleteThisBlog(){
    this.toastr.success('wellcome to Delete block');
    //subcriber code is here
    this.blogHttpService.deleteBlog(this.currentBlog.blogId).subscribe(
      data => {
        console.log(data);
        this.toastr.success('Blog Deleted successfully , "Thanks You"');
        this.currentBlog = data["data"];
        setTimeout(()=>{
          this.router.navigate(['/home']);

        }, 1000)
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
        this.toastr.error('Some error occour in deletion');
      }
    )
  }// deleteThisBlog end here

  public goBackToPreviousPage(): any{
    this.location.back();
  }


  ngOnDestroy() {
    console.log("blog Destroy function called is here");
    
  }



}// class end here


